@media screen and (min-width: 320px) {
  .footer-container {
    width: 320px;
    border: none;
  }
}

.list {
  list-style-type: none;
}

.footer {
  display: flex;
  /* padding-left: 20px;
  padding-right: 20px;*/
  background-color: #fff5f6;
  background-size: auto;
  background-repeat: no-repeat;
}

.footer-container {
  /*max-width: 280px;*/
  padding-top: 65px;
  display: flex;
}
.footer-section {
  justify-content: space-between;
  margin: auto;
}
.moto-text {
  justify-content: flex-start;
  padding-left: 20px;
  padding-right: 66px;
}

.moto-text-1 {
  font-family: $secondary-font;
  font-style: normal;
  font-weight: normal;
  font-size: 18px;
  line-height: 21px;
  text-transform: uppercase;
  color: #ffa5ba;
  padding-bottom: 15px;
}

.moto-text-2 {
  font-family: $secondary-font;
  font-style: normal;
  font-weight: normal;
  font-size: 34px;
  line-height: 39px;
  letter-spacing: 0.04em;
  text-transform: uppercase;
  color: #d41443;
}

.contacts {
  align-items: baseline;
  padding-top: 46px;
  justify-content: flex-end;
  text-decoration: none;
  /* padding-left: 30px;*/
  padding-bottom: 40px;
  text-align: end;
}

.social-media {
  padding-top: 43px;
}

.social-media-list {
  display: flex;
  padding-left: 147px;
  padding-right: 20px;
}
.social-media-list_item-link {
  display: block;
}
.social-media-list_item-link:hover .social-media-list_item-svg,
.social-media-list_item-link:focus .social-media-list_item-svg {
  fill: #d41342;
}

.social-media-list_item:not(:last-child) {
  padding-right: 24px;
}
.social-media-list_item-svg {
  fill: #fea5bb;
  transition: fill 250ms cubic-bezier(0.4, 0, 0.2, 1),
    background-color 250ms cubic-bezier(0.4, 0, 0.2, 1);
  &:hover,
  &:focus {
    fill: #d41443;
  }
}

.contacts-text {
  font-family: $main-font;
  font-style: normal;
  font-weight: bold;
  font-size: 12px;
  line-height: 186%;
  text-align: right;
  color: #000000;
  padding-left: 70px;
  padding-right: 20px;
}

.tel {
  font-family: $main-font;
  font-style: normal;
  font-weight: bold;
  font-size: 12px;
  line-height: 186%;
  text-align: right;
  color: #d41443;
  text-decoration: none;
}

.copywrite {
  display: block;
  padding-bottom: 25px;
  padding-top: 22px;
  height: 42px;
  left: 79px;
  top: 7049px;
  font-family: $main-font;
  font-style: normal;
  font-weight: bold;
  font-size: 14px;
  line-height: 152%;
  text-align: center;
  color: #907e82;
  align-items: center;
  box-sizing: content-box;
  border-top: 1px solid #e1e1e1;
}

.copywrite:before {
  content: '\00A9';
}

@media screen and (min-width: 768px) {
  /*.footer {
    justify-content: space-between;*/

  .footer-container {
    width: 768px;
    margin-left: 0;
    margin-right: 0;
    box-sizing: content-box;

    padding-left: 20px;

    .contacts-block-section {
      display: flex;
      padding-bottom: 80px;
      /*justify-content: space-around;*/
    }
    /*.contacts {
      width: 320px;
    }*/
  }
  /*.moto-text {
    /*padding-right: 60px;
    padding-left: 20px;
  }*/
  .moto-text-1 {
    width: 472px;
    height: 34px;
    left: 114px;
    top: 4923px;
    font-size: 30px;
    line-height: 34px;
    letter-spacing: 0.04em;
  }

  .moto-text-2 {
    width: 474px;
    height: 66px;
    left: 114px;
    top: 4972px;
    font-size: 58px;
    line-height: 66px;
    letter-spacing: 0.06em;
  }
  .social-media {
    padding-top: 0;
  }
  .social-media-list {
    /*padding-left: 198px;*/
    margin-top: 0;
    justify-content: flex-end;
    padding-right: 52px;
  }

  .contacts-text {
    width: 319px;
    height: 30px;
    left: 845px;
    top: 5007px;
    font-size: 16px;
    line-height: 30px;
    letter-spacing: 0.02em;
    padding-left: 0px;
    padding-right: 52px;
  }

  .tel {
    width: 230px;
    height: 22px;
    left: 70px;
    top: 6962px;
    font-size: 12px;
    line-height: 22px;
    text-align: right;
  }
  .copywrite {
    padding-right: 0;
    padding-top: 30px;
    box-sizing: content-box;
  }
}

@media screen and (min-width: 1280px) {
  .footer {
    margin: auto;
    padding-left: 115px;
    padding-right: 115px;
  }
  .footer-section {
    margin: auto;
  }

  .moto-text {
    display: inline-grid;
  }

  .moto-text-2 {
    font-size: 58px;
    line-height: 66px;
    letter-spacing: 0.06em;
    width: 474px;
  }
  .social-media-list {
    padding-right: 0;
  }
  .contacts-block-section {
    width: 1010px;
    justify-content: space-between;
  }
  .contacts-text {
    width: 319px;
    height: 30px;
    left: 845px;
    top: 5007px;
    font-size: 16px;
    line-height: 30px;
    letter-spacing: 0.02em;
    padding-right: 0;
  }

  .tel {
    width: 230px;
    height: 22px;
    left: 70px;
    top: 6962px;
    font-size: 12px;
    line-height: 22px;
  }
  .copywrite {
    padding-top: 95px;
  }
}
