* {
  margin: 0;
  padding: 0;
}

#slideshow-wrap {
  display: block;
  height: 350px;
  min-width: 260px;
  max-width: 926px;
  margin: auto;
  //   border: 12px rgba(255, 255, 240, 1) solid;
  //   -webkit-box-shadow: 0px 0px 5px rgba(0, 0, 0, 0.8);
  //   -moz-box-shadow: 0px 0px 5px rgba(0, 0, 0, 0.8);
  //   box-shadow: 0px 0px 5px rgba(0, 0, 0, 0.8);
  position: relative;
  margin-bottom: 234px;
  // padding-top: 123px;
}

#slideshow-inner {
  width: 100%;
  height: 100%;
  background-color: #ffffff;
  overflow: hidden;
  position: relative;
  //   padding-bottom: 234px;
  //   padding-top: 123px;
}

#slideshow-inner > ul {
  list-style: none;
  height: 100%;
  width: 500%;
  overflow: hidden;
  position: relative;
  left: 0px;
  -webkit-transition: left 0.8s cubic-bezier(0.77, 0, 0.175, 1);
  -moz-transition: left 0.8s cubic-bezier(0.77, 0, 0.175, 1);
  -o-transition: left 0.8s cubic-bezier(0.77, 0, 0.175, 1);
  transition: left 0.8s cubic-bezier(0.77, 0, 0.175, 1);
}

#slideshow-inner > ul > li {
  width: 20%;
  height: 320px;
  float: left;
  position: relative;
}

#slideshow-inner > ul > li > img {
  display: block;
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 15px;
  //   margin-left: 43%;
  //   height: 100%;
}

#slideshow-wrap input[type='radio'] {
  position: absolute;
  left: 50%;
  bottom: 15px;
  z-index: 100;
  visibility: hidden;
}

#slideshow-wrap label:not(.arrows):not(.show-description-label) {
  position: absolute;
  left: 50%;
  bottom: -45px;
  z-index: 100000;
  width: 15px;
  height: 15px;
  background-color: #d9d9d9;
  -webkit-border-radius: 50%;
  -moz-border-radius: 50%;
  border-radius: 50%;
  cursor: pointer;
  //   -webkit-box-shadow: 0px 0px 3px rgba(0, 0, 0, 0.8);
  //   -moz-box-shadow: 0px 0px 3px rgba(0, 0, 0, 0.8);
  //   box-shadow: 0px 0px 3px rgba(0, 0, 0, 0.8);
  -webkit-transition: background-color 0.2s;
  -moz-transition: background-color 0.2s;
  -o-transition: background-color 0.2s;
  transition: background-color 0.2s;
}

#slideshow-wrap label:not(.arrows):active {
  bottom: -46px;
}

#slideshow-wrap input[type='radio']#button-1:checked ~ label[for='button-1'] {
  background-color: transparent;
  background-image: url(../images/icon-home.svg);
  background-repeat: no-repeat;
  //   background-color: rgba(100, 100, 100, 1);
}

#slideshow-wrap input[type='radio']#button-2:checked ~ label[for='button-2'] {
  //   background-color: rgba(100, 100, 100, 1);
  background-color: transparent;
  background-image: url(../images/icon-home.svg);
  background-repeat: no-repeat;
}

#slideshow-wrap input[type='radio']#button-3:checked ~ label[for='button-3'] {
  //   background-color: rgba(100, 100, 100, 1);
  background-color: transparent;
  background-image: url(../images/icon-home.svg);
  background-repeat: no-repeat;
}

