.description {
  //   position: absolute;
  top: 0;
  left: 0;
  width: 926px;
  max-width: 100%;
  font-family: 'DM Sans', sans-serif;
  z-index: 1000;
}

.description input {
  visibility: hidden;
}

.description label {
  font-family: 'DM Sans', sans-serif;
  background-color: #fafafa;
  position: relative;
  left: -17px;
  top: 00px;
  width: 40px;
  max-width: 100%;
  height: 27px;
  display: inline-block;
  text-align: center;
  padding-top: 7px;
  border-bottom-right-radius: 15px;
  cursor: pointer;
  opacity: 0;

  -webkit-transition: opacity 0.2s;
  -moz-transition: opacity 0.2s;
  -o-transition: opacity 0.2s;
  transition: opacity 0.2s;
  z-index: 5;
  color: rgb(20, 20, 20);
}

#slideshow-inner > ul > li:hover .description label {
  opacity: 1;
}

.description input[type='checkbox']:checked ~ label {
  opacity: 1;
}

.description .description-text {
  display: block;
  background-color: #ffffff;
  padding-left: 15px;
  //   padding-top: 25px;
  padding-right: 15px;
  //   padding-bottom: 15px;
  position: relative;
  top: 50%;
  //   left: 50%;
  z-index: 4;
  opacity: 1;
  -webkit-transition: opacity 0.2s;
  -moz-transition: opacity 0.2s;
  -o-transition: opacity 0.2s;
  transition: opacity 0.2s;
  color: rgb(20, 20, 20);
}
.description-text-container {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #fafafa;
  width: 926px;
  max-width: 100%;
  &::after {
    position: absolute;
    content: '';
    border: 1px solid #e1e1e1;
  }
}
.description-text p {
  background-image: url(../images/symbol-cov.svg);
  background-repeat: no-repeat;
  background-position-y: center;
  width: 822px;
  max-width: 100%;
  box-sizing: border-box;
  display: flex;
  justify-content: center;
  align-items: center;
  max-width: 100%;
  height: 138px;
  margin-bottom: 20px;
  font-style: normal;
  font-weight: 500;
  font-size: 16px;
  line-height: 1.88;
  text-align: center;
  letter-spacing: 0.04em;
  color: #000000;
  text-decoration: underline solid #e1e1e1;
}
.description-text span {
  //   position: absolute;
  display: inline-block;
  width: 20px;
  max-width: 100%;
  height: 20px;
  color: #c7133e;
}
.description-text h2 {
  font-weight: 500;
  font-size: 20px;
  line-height: 1.85;
  text-align: center;

  color: #000000;
  font-family: inherit;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 40px;
}
