.icon-dots {
  position: absolute;
  left: 48%;
  margin-top: 15px;
  width: 20px;
  height: 20px;
  fill: #c7133e;
  z-index: 2000;
}
.description input[type='checkbox']:checked ~ .description-text {
  opacity: 1;
}
